
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import * as dd from 'dingtalk-jsapi';
export default {
data(){
return {

}

},
created(){
this.getToken()
this.getToken222()
dd.ready(function() {
dd.runtime.permission.requestAuthCode({
corpId: 'ding11b0951f7162175035c2f4657eb6378f', // id
onSuccess: function (info) {
console.log(info)
},
onFail : function(err) {
console.log(err)
}
})
})
},
methods:{
getToken(){
let corpid='ding11b0951f7162175035c2f4657eb6378f',
corpsecret='pVaEddIXBlE5w1BK6a7QYK-aIY_SoO-XdfnsNH2qm4kBsUujlm5-yn07t3JNKcrE'
axios({
method:'Get',
url:'https://oapi.dingtalk.com/gettoken?corpid='+corpid+'&corpsecret='+corpsecret,
}).then((res)=>{
console.log(res)
})
},
getToken222(){
let sAMAccountName='sdt1384144'

axios({
method:'GET',
url:'http://x.skyworthdigital.com:8000/oauth/WebService/WebService.asmx?sAMAccountName='+sAMAccountName,
}).then((res)=>{
console.log(res)
})
}
},
components: {
XHeader,
Flexbox,
FlexboxItem,
Tabbar,
TabbarItem
}
}



